import React from 'react'
import OtherLayout from '../components/Layout/OtherLayout';
import Ashir from '../images/About/aashir.png';
import '../Style/About/About.css';
import captain from '../images/About/captains.svg'
import transportation from '../images/About/transportation.svg'
import vendor from '../images/About/vendors.svg'
import Slider from 'react-slick';
import '../Style/Slider/Slider.css'

const About = () => {

    function SampleNextArrow(props) {
        const { className, style, onClick } = props;
        return (
            <div
                className={`${className} Slider`}
                style={{ ...style, display: "block", background: "#333333", width: "50px", height: "50px", display: "flex", justifyContent: "center", alignItems: "center", borderRadius: "50%" }}
                onClick={onClick}
            ><i class="fa-sharp fa-regular fa-arrow-left" style={{}}></i></div>
        );
    }

    function SamplePrevArrow(props) {
        const { className, style, onClick } = props;
        return (
            <div
                className={`${className} Slider`}
                style={{ ...style, display: "block", background: "#333333", width: "50px", height: "50px", display: "flex", justifyContent: "center", alignItems: "center", borderRadius: "50%", zIndex: "10" }}
                onClick={onClick}
            ><i class="fa-sharp fa-regular fa-arrow-right"></i></div>
        );
    }

    var settings = {
        dots: true,
        infinite: false,
        speed: 500,
        slidesToShow: 4,
        slidesToScroll: 3,
        initialSlide: 0,
        nextArrow: <SampleNextArrow />,
        prevArrow: <SamplePrevArrow />,
        responsive: [
            {
                breakpoint: 1024,
                settings: {
                    slidesToShow: 4.2,
                    slidesToScroll: 3,
                    infinite: true,
                    dots: true
                }
            },
            {
                breakpoint: 600,
                settings: {
                    slidesToShow: 3.2,
                    slidesToScroll: 2,
                    initialSlide: 2
                }
            },
            {
                breakpoint: 480,
                settings: {
                    slidesToShow: 1.3,
                    slidesToScroll: 1
                }
            }
        ]
    };

    const warriors = [
        {
            img: `${captain}`,
            title: "Trip Captains",
            desc: "Our trip captains are nothing short of heroes. Brave, dynamic, and great leaders, theyre certainly the showrunners of the community trips. Theyve been handpicked from Indias best travel institutes, and know all tricks of trade to lead and manage all kinds of trips, including biking and trekking expeditions."
        },
        {
            img: `${transportation}`,
            title: "Local Vendors",
            desc: "Being the bridge between the local vendors and the customers, were very selective about the former. After a rigorous recci, weve found the most kind and hospitable vendors, who make our travelers feel at home, even when they might be miles away from it. Over the period of time, weve managed to make a family-like relationship with our vendors, thus giving back to the local community in every way possible!"
        },
        {
            img: `${vendor}`,
            title: "Transport Drivers",
            desc: "We understand the responsibility of taking utmost care of our travelers when theyre off to experience the toughest terrains of the country. Hence everything depends on the competency of our drivers. All the drivers at WanderOn are local professionals, whove been in practice since years, and we assure that they know all nooks and corners of the roads by heart!"
        },
    ]

    return (
        <>
            <OtherLayout title="ABOUT US">
                <div className="container p-5 text-center Mon_font">
                    <h3 className='about__heading'>What do we stand for and how did we reach to it?</h3>
                    <p className='m-0 text-secondary text-start'>Remember the days when we used to fill our slam-books with career aspirations like scientist, teacher and doctor? Nobody at that time thought there couldve been a career in Travelling, let alone being a travelpreneur! But as life happens, you understand that a career could be anything where you can be a problem-solver for the society.
                        And thats how a few engineers from NIT Kurukshetra found that the travel industry in India needed a fresh burst of young energy! The need of the hour was to convert a dispersed agent based model to a more friendly ,transparent and an accessible community for Indian travellers, and hence WanderOn. Lets have a closer look at the hustlers whore on a mission to stir up peoples life with memorable experiences.</p>
                </div>

                <div className="container p-5 text-center Mon_font">
                    <h2 className='about__heading'>Meet Our Team. The ideal set of extraordinary people..</h2>
                    <div className="row">
                        <Slider {...settings}>
                            {
                                [1, 2, 3, 4, 5, 6].map(() => {
                                    return <div className="my-lg-4 my-0 col-12 col-sm-12 col-md-6 col-lg-4 p-4">
                                        <div className="team__box Mon_font">
                                            <img width={150} src={Ashir} alt="" />
                                            <h4 className='mt-3 m-0 fs-3'>Ashir Ansari</h4>
                                            <p className='m-0 fs-4 fw-100 text-secondary'>CEO, Founder</p>
                                            <div>
                                                <i className='fa-brands fa-instagram'></i>
                                                <i className='fa-brands fa-linkedin'></i>
                                                <i className='fa-brands fa-facebook'></i>
                                            </div>
                                        </div>
                                    </div>
                                })
                            }
                        </Slider>
                    </div>

                    <div className='container our__warriors Mon_font p-lg-5 p-sm-2 py-5'>
                        <h3 className='about__heading my-4'>Our WARRIORS who have helped us build this holistic community</h3>
                        <div className="row">
                            {
                                warriors.map((e) => {

                                    return <>
                                        <div className="text-center col-12 col-sm-12 col-md-6 col-lg-4">
                                            <img className='my-3' width={100} src={e.img} alt="" />
                                            <h4 className='about__heading'>{e.title}</h4>
                                            <hr className='m-2 p-0' />
                                            <p className="text-start m-0 p-0">{e.desc}</p>
                                        </div>
                                    </>
                                })
                            }
                        </div>
                    </div>
                </div>

            </OtherLayout>
        </>
    )
}

export default About
