import React from 'react'
import { useNavigate } from 'react-router-dom';
import Slider from "react-slick";
import bloating from '../../images/LandingPage/boating-in-meghalaya.webp';
import '../../Style/Slider/Slider.css'

const TripDetails = ({ data, title }) => {
    const navigate = useNavigate();

    function SampleNextArrow(props) {
        const { className, style, onClick } = props;
        return (
            <div
                className={`${className} Slider`}
                style={{ ...style, background: "#333333", width: "50px", height: "50px", display: "flex", justifyContent: "center", alignItems: "center", borderRadius: "50%" }}
                onClick={onClick}
            >
                <i class="fa-thin fa-chevron-left"></i>
                {/* <i className="fa-sharp fa-regular fa-arrow-left"></i> */}
            </div>
        );
    }

    function SamplePrevArrow(props) {
        const { className, style, onClick } = props;
        return (
            <div
                className={`${className} Slider`}
                style={{ ...style, display: "block", background: "#333333", width: "50px", height: "50px", display: "flex", justifyContent: "center", alignItems: "center", borderRadius: "50%", zIndex: "10" }}
                onClick={onClick}
            >
                {/* <i className="fa-sharp fa-regular fa-arrow-right"></i> */}
            </div>
        );
    }

    var settings = {
        dots: true,
        infinite: false,
        speed: 500,
        slidesToShow: 4,
        slidesToScroll: 1,
        initialSlide: 0,
        // nextArrow: <SampleNextArrow />,
        // prevArrow: <SamplePrevArrow />,
        responsive: [
            {
                breakpoint: 1024,
                settings: {
                    slidesToShow: 4,
                    slidesToScroll: 3,
                    infinite: true,
                    dots: true
                }
            },
            {
                breakpoint: 600,
                settings: {
                    slidesToShow: 3.2,
                    slidesToScroll: 2,
                    initialSlide: 2
                }
            },
            {
                breakpoint: 480,
                settings: {
                    slidesToShow: 1.3,
                    slidesToScroll: 1
                }
            }
        ]
    };

    return (
        <div className="contaniner-fluid upcoming__trips px-lg-5 px-2 mb-5">
            <span className="category__heading ps-2">{title}</span>
            {
                title === "Upcoming Trips" && <select name="date" id="">
                    <option value="March 2023">Mar 2023</option>
                    <option value="April 2023">Apr 2023</option>
                    <option value="May 2023">May 2023</option>
                </select>
            }

            <div className="trips__container">
                <Slider {...settings}>
                    {
                        data.map((elm, ind) => (
                            <div key={ind} className="trips" onClick={() => navigate(`/singletrip/${elm.id}`)}>
                                <img src={bloating} alt="" className="trip__image" />
                                <p className="date__time p-0"><span><i className="fa-regular fa-clock me-2"></i>{elm.durationOfTrip}</span><i className="ms-3 me-2 fa-solid fa-location-dot"></i>{elm.locationOfDeparture}</p>
                                <h5 className="trip__title">{elm.tripHeading}</h5>
                                <span className='text-secondary'><small>Starts at</small><h5 className='ms-2 d-inline'> {elm.cost}/-</h5></span>
                                <p className="trip__date"><i className="fa-regular fa-calendar me-2"></i>{elm.dates[0].march[0]}</p>
                            </div>
                        ))
                    }
                </Slider>
            </div>
        </div>
    )
}

export default TripDetails
