import './App.css';
import LandingPage from './pages/LandingPage';
import About from './pages/About'
import Contact from './pages/Contact';
import Blog from './pages/Blog'
import SingleTrip from './pages/SingleTrip';
import BookingPage from './pages/BookingPage';
import Payment from './pages/Payment'
import ScrollToTop from './components/ScrollToTop';
import { BrowserRouter, Route, Routes } from 'react-router-dom';
import ProtectedRoute from './pages/admin/ProtectedRoute';
import AdminDashboard from './pages/admin/AdminDashboard';
import AdminLogin from './pages/admin/AdminLogin';
import AddTrip from './pages/admin/AddTrip';

function App() {
  return (
    <BrowserRouter>
      <ScrollToTop />
      <main>
        <Routes>
          <Route exact path='/admin-dashboard' element={<ProtectedRoute><AdminDashboard /></ProtectedRoute>} />
          <Route exact path='/login' element={<AdminLogin />} />
          <Route exact path='/add-trip' element={<ProtectedRoute><AddTrip /></ProtectedRoute>} />
          <Route exact path='/' element={<LandingPage />} />
          <Route exact path='/about' element={<About />} />
          <Route exact path='/contact' element={<Contact />} />
          <Route exact path='/blog' element={<Blog />} />
          <Route exact path='/singletrip/:id' element={<SingleTrip />} />
          <Route exact path='/book/:id' element={<BookingPage />} />
          <Route exact path='/payment' element={<Payment />} />
        </Routes>
      </main>
    </BrowserRouter>
  );
}

export default App;
