import React from 'react';
import Footer from '../Footer/Footer';
import Header from '../Header/Header';
import HeroSection from '../LandingPage/HeroSection';
import '../../Style/OtherLayout/OtherLayout.css';
import { useLocation } from 'react-router-dom';

const OtherLayout = ({ children, title, subTitle }) => {

    const location = useLocation();
    const isAbout = location.pathname === '/about';
    const isContact = location.pathname === '/contact';
    const isBlog = location.pathname === '/blog';
    return (
        <div className='main_landing'>
            {/* we can add background from url in below container using style  */}
            <div className={`other_main_bg_cover`}>
                <Header />
                {
                    isAbout && <h2 className='hero__heading text-white'>{title}</h2>
                }
                {
                    isContact && <h2 className='hero__heading text-white'>{title}</h2>
                }
                {
                    isBlog && <h2 className='hero__heading text-white'>{title}</h2>
                }
                {
                    !isAbout && !isContact && !isBlog && <HeroSection title={title} subTitle={subTitle} />
                }
            </div>
            <div>{children}</div>
            <Footer />
        </div>

    )
}

export default OtherLayout
