import React from 'react';
import "../../Style/Admin/AdminDashboard.css";
import { Link, useLocation, useNavigate } from "react-router-dom";
import { message } from 'antd';
import axios from 'axios';

const Layout = ({ children }) => {

    const navigate = useNavigate();

    // Logout Handler 
    const onHandleLogout = () => {
        axios({
            method: "POST",
            withCredentials: true,
            url: "http://localhost:5000/api/users/logout",
        }).then((res) => {
            message.success("Logout successful")
            navigate('/login');
        }).catch((err) => {
            console.log(err);
        });
    }

    // ===================================== ADMIN MENU ========================================
    const adminMenu = [
        {
            name: "Add Trip",
            path: '/add-trip',
            icon: "fa-solid fa-user"
        },
        {
            name: "Support",
            path: "/support",
            icon: "fa-solid fa-user"
        },
    ]
    // ===================================== ADMIN MENU ========================================

    return (
        <>
            <div className="adminMain">
                <div className="adminLayout">
                    <div className="adminSidebar d-none d-lg-block">
                        <div className='d-flex justify-content-between align-items-center p-4'>
                            <h5 className='text-white'>Hello Admin</h5>
                            <button className='btn btn-warning' onClick={onHandleLogout}>Logout</button>
                        </div>
                        <hr className='m-2' />
                        {
                            adminMenu.map((menu) => (
                                <Link to={menu.path}>
                                    <p className='Mon_font'>{menu.name}</p>
                                    <hr />
                                </Link>
                            ))
                        }
                    </div>
                    <div className="adminBody p-2 p-lg-5">
                        {children}
                    </div>
                </div>
            </div>
        </>
    )
}

export default Layout
