import React from 'react'

const ContactForm = () => {
    return (
        <div className="contact__form border p-3 rounded">
            <p className='text-center Mon_font fs-lg-5 fs-sm-6'>Trips so perfect that Travelers want you to know more about their experiences!</p>
            <div className="light__text__cont">
                <h2 className='Mon_font'>get in touch<span className='light_text'>CONTACT</span></h2>
            </div>
            <form>
                <div className="mb-3">
                    <label for="FullName" className="form-label">Full Name</label>
                    <input type="email" className="form-control" aria-describedby="emailHelp" />
                </div>
                <div className="mb-3">
                    <label for="Contact" className="form-label">Contact Number</label>
                    <input type="text" className="form-control" aria-describedby="emailHelp" />
                </div>
                <div className="mb-3">
                    <label for="Email" className="form-label">Your Email</label>
                    <input type="text" className="form-control" aria-describedby="emailHelp" />
                </div>
                <div className="mb-3">
                    <label for="Destination" className="form-label">Your Destination</label>
                    <input type="text" className="form-control" aria-describedby="emailHelp" />
                </div>
                <button type="submit" className="btn btn-warning d-block m-auto">Submit</button>
            </form>
        </div>
    )
}

export default ContactForm
