import axios from 'axios';
import React, { useEffect, useState } from 'react'
import { useNavigate } from 'react-router-dom';
import '../../Style/Admin/AdminLogin.css'

const AdminLogin = () => {
    const navigate = useNavigate();
    const [isLogin, setIsLogin] = useState(false);
    const [email, setEmail] = useState(null);
    const [pass, setPass] = useState(null);

    useEffect(() => {
        axios({
            method: "get",
            withCredentials: true,
            url: `http://localhost:5000/api/users/isLogin`,
        }).then((res) => {
            setIsLogin(false)
            if (res.data.success) {
                navigate('/admin-dashboard');
            }
        }).catch((err) => {
            setIsLogin(true)
            console.log(err);
        });
        // eslint-disable-next-line
    }, [])

    const handleSubmit = async (e) => {
        e.preventDefault();
        axios({
            method: "POST",
            data: {
                email,
                pass
            },
            withCredentials: true,
            url: `http://localhost:5000/api/users/login`,
        }).then((res) => {
            if (res.data.success) {
                navigate('/admin-dashboard');
            }
        }).catch((err) => {
            alert(err.message);
        });
    }
    return (
        <>
            {isLogin &&
                <div className='form__body Mon_font'>
                    <form onSubmit={handleSubmit} className='border text-white p-5 rounded'>
                        <div class="mb-3">
                            <label for="exampleInputEmail1" class="form-label">Email address</label>
                            <input type="email" class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" onChange={(e) => setEmail(e.target.value)} required placeholder='Enter email' />
                        </div>
                        <div class="mb-3">
                            <label for="exampleInputPassword1" class="form-label">Password</label>
                            <input type="password" class="form-control" id="exampleInputPassword1" onChange={(e) => setPass(e.target.value)} required placeholder='Enter password' />
                        </div>
                        <button type="submit" class="btn btn-warning d-block m-auto">Login</button>
                    </form>
                </div>
            }
        </>
    )
}

export default AdminLogin
