import React from 'react';
import '../../Style/SingleTrip/DateAndCosting.css'

const DatesAndCosting = () => {
    return (
        <>
            <div className='d-flex'>
                {
                    ["march", "april", "may"].map((elm) => (
                        <div className='Mon_font circle mx-2'>
                            <span>{elm}</span>
                        </div>
                    ))
                }
            </div>
            <div className='avail__dates Mon_font my-5 row px-2'>
                <div className="col-6">
                    <h4>31 March 2023 - 04 April 2023 {"(open)"}</h4>
                </div>
                <div className="col-6">
                    <h4>31 March 2023 - 04 April 2023 {"1 Seat Left"}</h4>
                </div>
            </div>
            <div className='light__text__cont'>
                <h2 className='Mon_font'>costing <span className='light_text'>COSTING</span></h2>
            </div>
            <table class="table">
                <thead class="table-dark border" style={{ borderRadius: "15px" }}>
                    <tr>
                        <th scope="col">Room Sharing</th>
                        <th scope="col">Cost (per person)</th>
                    </tr>
                </thead>
                <tbody className='bg-light border'>
                    <tr>
                        <th scope="row">Double Sharing</th>
                        <td className='text-center'>8999/-</td>
                    </tr>
                    <tr>
                        <th scope="row">Triple Sharing</th>
                        <td className='text-center'>8499/-</td>
                    </tr>
                    <tr>
                        <th scope="row">Quad Sharing</th>
                        <td className='text-center'>7999/-</td>
                    </tr>
                </tbody>
            </table>

            <div className='inclusion'>
                <div className='light__text__cont'>
                    <h2 className='Mon_font'>inclusion <span className='light_text'>INCLUSION</span></h2>
                </div>
                <p></p>
            </div>
            <div className='light__text__cont'>
                <h2 className='Mon_font'>exclusions <span className='light_text'>EXCLUSION</span></h2>
            </div>
        </>

    )
}

export default DatesAndCosting
