import React, { useState } from 'react'
import { useNavigate, useParams } from 'react-router-dom'
import Footer from '../components/Footer/Footer';
import '../Style/BookingPage/Booking.css'

const BookingPage = () => {
    const navigate = useNavigate();
    const params = useParams();
    const [toggle, setToggle] = useState(1);
    const [date, setDate] = useState(false);
    const [dcount, setDcount] = useState(0);
    const [tcount, setTcount] = useState(0);
    const [qcount, setQcount] = useState(0);

    return (
        <>
            <div className='mb-5 booking__page Mon_font mt-5 text-center px-lg-5 px-sm-2 p-3'>
                {/* date container  */}
                <h1 className={`mb-3 fs-sm-4 fs-lg-2 ${toggle === 1 ? "show" : "content"}`}>Please Select your Batch Dates</h1>
                <div className={`book__cont container pb-3 p-0 ${toggle === 1 ? "show" : "content"}`}>
                    <h3 className='text-white bg-dark p-3 rounded'>May</h3>
                    <div className='my-5 row text-center'>
                        <div className="date__cont py-2 col-12 col-sm-12 col-md-6 col-lg-6 d-flex justify-content-center">
                            <div className="border__date d-flex">
                                <div>
                                    <p className='text-start m-0 me-2'><b className='me-2'>Start Date:</b>12 March 2023</p>
                                    <p className='text-start m-0 me-2'><b className='me-2'>End Date:</b>17 March 2023</p>
                                </div>
                                <input onChange={() => setDate(true)} className='radio' type="radio" name="March" id="" />
                            </div>
                        </div>
                    </div>
                </div>
                {/* date container  */}

                {/* Room container  */}
                <h1 className={`mb-3 fs-sm-4 fs-lg-2 ${toggle === 2 ? "show" : "content"}`}>Please Select your Room Sharing</h1>
                <div className={`book__cont container pb-3 p-0 ${toggle === 2 ? "show" : "content"}`}>
                    <h3 className='text-white bg-dark p-3'>Room Sharing</h3>
                    <div className='d-flex justify-content-center'>
                        <table class="table">
                            <thead>
                                <tr>
                                    <th className='book_th' scope="col">Type</th>
                                    <th className='book_th' scope="col">Price (per person)</th>
                                    <th className='book_th' scope="col"><i className='fa fa-user'></i></th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr>
                                    <th className='book_th' scope="row t_data">Double Sharing</th>
                                    <td>8999/-</td>
                                    <td>
                                        <button style={{ border: "0" }} onClick={() => {
                                            setDcount(dcount - 1);
                                            if (dcount === 0) {
                                                setDate(false);
                                            }
                                        }} className={`btn ${dcount === 0 ? "disabled" : ""} m-0 p-0`}>
                                            <i className='fa-solid fa-minus'></i>
                                        </button>
                                        <span className='mx-2'>{dcount}</span>
                                        <i onClick={() => {
                                            setDcount(dcount + 1);
                                            setDate(true)
                                        }} className='fa-solid fa-add'></i>
                                    </td>
                                </tr>
                                <tr>
                                    <th className='book_th' scope="row t_data">Triple Sharing</th>
                                    <td>8499/-</td>
                                    <td>
                                        <button style={{ border: "0" }} onClick={() => setTcount(tcount - 1)} className={`btn ${tcount === 0 ? "disabled" : ""} m-0 p-0`}>
                                            <i className='fa-solid fa-minus'></i>
                                        </button>
                                        <span className='mx-2'>{tcount}</span>
                                        <i onClick={() => setTcount(tcount + 1)} className='fa-solid fa-add'></i>
                                    </td>
                                </tr>
                                <tr>
                                    <th className='book_th' scope="row t_data">Quad Sharing</th>
                                    <td>7999/-</td>
                                    <td>
                                        <button style={{ border: "0" }} onClick={() => setQcount(qcount - 1)} className={`btn ${qcount === 0 ? "disabled" : ""} m-0 p-0`}>
                                            <i className='fa-solid fa-minus'></i>
                                        </button>
                                        <span className='mx-2'>{qcount}</span>
                                        <i onClick={() => setQcount(qcount + 1)} className='fa-solid fa-add'></i>
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
                {/* Room container  */}

                {/* User container  */}
                <h1 className={`mb-3 fs-sm-4 fs-lg-2 ${toggle === 3 ? "show" : "content"}`}>Please Provide your Details</h1>
                <div className={`book__cont container pb-3 p-0 ${toggle === 3 ? "show" : "content"}`}>
                    <h3 className='text-white bg-dark p-3'>User Details</h3>
                    <form className='p-5 d-block m-auto' style={{ maxWidth: "500px" }}>
                        <div class="mb-3 text-start">
                            <label for="username" class="form-label">Your Name</label>
                            <input type="text" class="form-control" id="username" aria-describedby="username" />
                        </div>
                        <div class="mb-3 text-start">
                            <label for="Email1" class="form-label">Email address</label>
                            <input type="email" class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" />
                        </div>
                        <div class="mb-3 text-start">
                            <label for="contact" class="form-label">Contact Number</label>
                            <input type="text" class="form-control" id="contact" aria-describedby="contact" />
                        </div>
                        <div class="mb-3 text-start">
                            <label for="address" class="form-label">Address</label>
                            <input type="text" class="form-control" id="address" aria-describedby="address" />
                        </div>
                    </form>
                </div>
                {/* User container  */}

                <div className='d-flex justify-content-evenly mt-4'>
                    {
                        toggle === 1 ?
                            <button className='btn btn-dark' onClick={() => navigate(`/singletrip/${params.id}`)}>Back</button>
                            :
                            <button className={`btn btn-dark text-white ${toggle === 1 ? "disabled" : ""}`} onClick={() => setToggle(toggle - 1)} >Back</button>
                    }
                    <div>
                        <i className={`${toggle === 1 ? "fa-sharp fa-solid fa-circle-dot" : "fa-sharp fa-regular fa-circle-dot"}`}></i>
                        <i className={`mx-3 ${toggle === 2 ? "fa-sharp fa-solid fa-circle-dot" : "fa-sharp fa-regular fa-circle-dot"}`}></i>
                        <i className={`${toggle === 3 ? "fa-sharp fa-solid fa-circle-dot" : "fa-sharp fa-regular fa-circle-dot"}`}></i>
                    </div>
                    {
                        toggle === 3 ?
                            <button className='btn btn-dark' onClick={() => navigate('/payment')}>Next</button>
                            :
                            <button className={`btn btn-dark text-white ${date ? "" : "disabled"}`} onClick={() => {
                                setToggle(toggle + 1);
                                setDate(false);
                            }} >Next</button>
                    }
                </div>
            </div>
            <Footer />
        </>
    )
}

export default BookingPage
