import React from 'react'
import jeep from '../../images/LandingPage/jeep.png';
import motorcycle from '../../images/LandingPage/motorcycle.png'
import backpacking from '../../images/LandingPage/backpacking-trips.svg'
import hiking from '../../images/LandingPage/hiking.png'
import trips from '../../images/LandingPage/customised-trips.svg'
import weekend from '../../images/LandingPage/weekend-trips.svg'

const Explore = () => {
    return (
        <div className="category__container row d-flex mt-lg-5 mt-4">
            <div className="col-4 col-sm-4 col-md-4 col-lg-2 category__feature p-3">
                <img src={motorcycle} alt="img" />
                <span className='content_text_grey my-2'>Leh Ladakh <br /> 2023</span>
            </div>
            <div className="col-4 col-sm-4 col-md-4 col-lg-2 category__feature p-3">
                <img src={jeep} alt="img" />
                <span className='content_text_grey my-2'>SPiti Valley <br /> 2023</span>
            </div>
            <div className="col-4 col-sm-4 col-md-4 col-lg-2 category__feature p-3">
                <img src={hiking} alt="img" />
                <span className='content_text_grey my-2'>Good Friday <br /> 2023</span>
            </div>
            <div className="col-4 col-sm-4 col-md-4 col-lg-2 category__feature p-3">
                <img src={backpacking} alt="img" />
                <span className='content_text_grey my-2'>Backpacking Trips <br /> 2023</span>
            </div>
            <div className="col-4 col-sm-4 col-md-4 col-lg-2 category__feature p-3">
                <img src={weekend} alt="img" />
                <span className='content_text_grey my-2'>Weekend Trips <br /> 2023</span>
            </div>
            <div className="col-4 col-sm-4 col-md-4 col-lg-2 category__feature p-3">
                <img src={trips} alt="img" />
                <span className='content_text_grey my-2'>International Trips <br /> 2023</span>
            </div>
        </div>
    )
}

export default Explore
