import React from 'react'
import { Link } from 'react-router-dom';
import '../../Style/SideMenu/style.css'

const SideMenu = ({ sideMenu }) => {
    return (
        <div className={`${sideMenu ? "sideMenu sideMenu__open" : "sideMenu"} sticky-top top-sticky`}>
            <ul>
                <li>
                    <Link to='/about'>Leh Ladakh 2023</Link>
                </li>
                <li>
                    <Link to='/about'>Spiti Valley 2023</Link>
                </li>
                <li>
                    <Link to='/about'>Backpacking Trips</Link>
                </li>
                <li>
                    <Link to='/about'>Weekend Trips</Link>
                </li>
                <li>
                    <Link to='/blog'>Blogs</Link>
                </li>
                {/* <li>
                    <Link to='/contact'>Contact Us</Link>
                </li> */}
                <li>
                    <Link to='/about'>About Us</Link>
                </li>
                <button className='btn btn-warning'><small>Contact Us</small></button>
            </ul>
        </div>
    )
}

export default SideMenu
