import React from 'react';
import { Location, useLocation } from 'react-router-dom';


const HeroSection = ({ title, subTitle }) => {

    const location = useLocation();
    const isHome = location.pathname === "/";

    return (
        <div className={`hero__section ${!isHome ? 'other__section' : ''}`}>
            <div className="main__heading text-white text-center">
                <h1 className='hero__heading'>{title}</h1>
                <h3 className='sub__heading'>{subTitle}</h3>
            </div>

            {/* search input only for homepage */}
            {
                isHome &&
                <div className="search__location">
                    <input type="search" className="search__field" placeholder='Where do you wanna go?' />
                    <i className='fa-solid fa-magnifying-glass'></i>
                </div>
            }

            <div className={`site__review`}>
                <div className='img__review text-white'>
                    <img src="https://www.linkpicture.com/q/cover-travellers.svg" alt="" />
                    <span>50000+</span>
                    <span>Happy Travelers</span>
                </div>
                <div className='img__review text-white'>
                    <img src="https://www.linkpicture.com/q/cover-star.svg" alt="" />
                    <span>50000+</span>
                    <span>Happy Travelers</span>
                </div>
                <div className='img__review text-white'>
                    <img src="https://www.linkpicture.com/q/cover-destination.svg" alt="" />
                    <span>50000+</span>
                    <span>Happy Travelers</span>
                </div>
            </div>
        </div>
    )
}

export default HeroSection
