import React, { useState } from 'react'
import { Link } from 'react-router-dom'
import '../../Style/Header/Header.css'
import BackDrop from '../SideMenu/BackDrop'
import SideMenu from '../SideMenu/SideMenu'

const Header = () => {
    const [navbar, setNavbar] = useState(false)
    const [sideMenu, setSidemenu] = useState(false)

    const NavbarBgChange = () => {
        if (window.scrollY >= 60) {
            setNavbar(true)
        } else {
            setNavbar(false)
        }
    }
    window.addEventListener('scroll', NavbarBgChange)

    const toggleMenu = () => {
        setSidemenu(!sideMenu)
    }

    return (
        <>
            <BackDrop sideMenu={sideMenu} closeSideMenu={toggleMenu} />
            <SideMenu sideMenu={sideMenu} />
            <nav className={`main navbar ${navbar ? "active" : ""} fixed-top`}>
                <Link to='/'>
                    <div className="logo d-flex align-items-center">
                        <i className='me-2 fa-solid fa-user'></i>
                        LOGO
                    </div>
                </Link>
                <div className="contact_number"><span><i className='me-2 fa-solid fa-phone'></i>+91 9284263003</span></div>
                <div className="menu_list d-none d-lg-block">
                    <ul className='d-flex'>
                        <li>
                            <Link to='/location'>LEH LADAKH 2023</Link>
                        </li>
                        <li>
                            <Link to='/about'>SPITI VALLEY 2023</Link>
                        </li>
                        <li>
                            <Link to='/blog'>BLOGS</Link>
                        </li>
                        <li>
                            <Link to='/about'>ABOUT US</Link>
                        </li>
                    </ul>
                </div>
                <div className="nav_toggle_button d-lg-none" onClick={toggleMenu}>
                    {
                        sideMenu ?
                            <i class="fa-solid fa-xmark"></i>
                            :
                            <i className="fa-solid fa-bars"></i>
                    }
                </div>
            </nav>
        </>

    )
}

export default Header
